/* eslint-disable no-eval */

import { useEffect, useState } from "react";

import "./Calculator.css";

import { Button } from "../Button/Button";
import { Header } from "../Header/Header";
import { Console } from "../Console/Console";

let evalOp = (operation) => {
  operation = operation.replaceAll("x", "*");
  operation = operation.replaceAll(",", ".");
  let result
  try {
    result = eval(operation)
  }
  catch {
    result = ["Syntax Error !"]
  }
  operation === "0/0" ? result = ["ZeroDivisionError: division by zero"] : result = result;
  return result;
};



export function Calculator(props) {
  let [theme, setTheme] = useState("theme-1");
  let [argText, setArgText] = useState("");

  useEffect(() => {
    argText.length === 17 ? setArgText(argText.slice(0, argText.length - 1)) : setArgText(argText)
  }, [argText, setArgText])

  document.getElementById('root').className = theme

  return (
    <div className="container">
      <Header theme={theme} changeTheme={setTheme} />
      <Console className={theme} argText={argText} />
      <div className={`buttons ${theme}`}>
          <div>
            <Button
              className={`button ${theme}`}
              onClick={(event) => {
                setArgText((argText += "7"));
              }}
            >
              7
            </Button>
            <Button
              onClick={(event) => {
                setArgText((argText += "8"));
              }}
              className={`button ${theme}`}
            >
              8
            </Button>
            <Button
              onClick={(event) => {
                setArgText((argText += "9"));
              }}
              className={`button ${theme}`}
            >
              9
            </Button>
            <Button

              onClick={(event) => {
                if (argText === "string") { argText.slice(0, argText.length - 1) };
              }}
              className={`button del ${theme}`}
            >
              DEL
            </Button>

          </div>
          <div>
            <Button

              onClick={(event) => {
                setArgText((argText += "4"));
              }}
              className={`button ${theme}`}
            >
              4
            </Button>
            <Button

              onClick={(event) => {
                setArgText((argText += "5"));
              }}
              className={`button ${theme}`}
            >
              5
            </Button>
            <Button


              onClick={(event) => setArgText((argText += "6"))}
              className={`button ${theme}`}
            >
              6
            </Button>
            <Button

              onClick={(event) => {
                setArgText((argText += "+"));
              }}
              className={`button ${theme}`}
            >
              +
            </Button>
          </div>
          <div>
            <Button

              onClick={(event) => {
                setArgText((argText += "1"));
              }}
              className={`button ${theme}`}
            >
              1
            </Button>
            <Button

              onClick={(event) => {
                setArgText((argText += "2"));
              }}
              className={`button ${theme}`}
            >
              2
            </Button>
            <Button

              onClick={(event) => {
                setArgText((argText += "3"));
              }}
              className={`button ${theme}`}
            >
              3
            </Button>
            <Button

              onClick={(event) => {
                setArgText((argText += "-"));
              }}
              className={`button ${theme}`}
            >
              -
            </Button>

          </div>
          <div>
            <Button

              onClick={(event) => {
                setArgText((argText += ","));
              }}
              className={`button ${theme}`}
            >
              .
            </Button>
            <Button

              onClick={(event) => {
                setArgText((argText += "0"));
              }}
              className={`button ${theme}`}
            >
              0
            </Button>
            <Button

              onClick={(event) => {
                setArgText((argText += "/"));
              }}
              className={`button ${theme}`}
            >
              /
            </Button>
            <Button

              onClick={(event) => {
                setArgText((argText += "x"));
              }}
              className={`button ${theme}`}
            >
              x
            </Button>

          </div>
          <div>
            <Button

              onClick={(event) => {
                setArgText((argText = ""));
              }}
              className={`button reset  ${theme}`}
            >
              RESET
            </Button>
            <Button

              onClick={(event) => {
                setArgText(evalOp(argText));
              }}
              className={`button equal  ${theme}`}
            >
              =
            </Button>
          </div>
      </div>
    </div>
  );
}




